import React from 'react'
import PropTypes from 'prop-types'
import './dates.css'

export default function Dates(props) {
  const { pointDate } = props
  return (
    <div className="dates">
      <div>
&nbsp;
        {pointDate.from ? new Date(pointDate.from).toLocaleDateString() : '  '}
      </div>
      <div>
&nbsp;
        {pointDate.to ? new Date(pointDate.to).toLocaleDateString() : '  '}
      </div>
    </div>
  )
}

Dates.propTypes = {
  pointDate: PropTypes.object,
}
